* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {

    font-size: 62.5%;
}

body {
    font-family: arial, helvetica, sans-serif;
    line-height: 1;
    font-weight: 400;
    color: #555;
}

@media (max-width: 48em) {
    html {
        font-size: 50%;
    }
}
