.container {
  max-width: 144rem;
  display: flex;
  flex-direction: column;
  padding: 0 4rem;
  margin: 0 auto 2rem;
}

.list {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  line-height: inherit; /* line height can be inherited or changed , while gap works only on flex-container, and can't be changed */
}

.list li {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.cityName {
  font-size: 3.6rem;
  color: #333333;
  font-weight: 600;
  word-break: break-word;
  letter-spacing: -0.5px;
  text-align: center;
  padding: 0 1.6rem;
}

.text {
  font-size: 1.6rem;
  font-weight: 400;
}

.padding {
  padding-left: 0.3rem;
}
